import React from 'react';
import './App.css';
import { Map2GIS } from 'react-2gis';
import { Map as MapGl } from '@2gis/mapgl/types';

const initialMapOptions = {
  center: [82.920412, 55.030111],
  zoom: 11,
  minZoom: 2,
  collectStats: true,
  sendAnalytics: false,
  key: '4970330e-7f1c-4921-808c-0eb7c4e63001',
  disableRotationByUserInteraction: false,
  disablePitchByUserInteraction: false,
  touchRotationThreshold: 10,
  styleState: {},
  hillshade: {},
  keepCenterWhileUserZoomRotate: false,
}

let map: MapGl | undefined = undefined;

const setMapInstance = (mapInstance: MapGl) => {
  map = mapInstance;
  (window as any).map = mapInstance;
}

const broadcast = new BroadcastChannel('2gis-update-channel');

broadcast.onmessage = (event) => {
  if (event.data && event.data.type === 'TILES_CAN_UPDATE') {
    map?.once('idle', () => {
      if (map) {
        const hashs = Object.keys((map as any)._impl.modules.assetManager.metatiles);
        hashs.forEach((el) => {
          +el > 0 && (map as any)._impl.modules.assetManager.metatileLoader(el, 1)
        });
        (map as any)._impl.modules.tileManager.redraw();
      }
    });
  }
};

function App() {

  return (
    <div className="App">
      <header className="App-header">
        <Map2GIS initialMapOptions={initialMapOptions} onMount={setMapInstance}></Map2GIS>
      </header>
    </div>
  );
}

export default App;
